.progressLabel {
  text-align: center;
}

.progressBar {
  width: 40%;
  background-color: grey;
  border: 1px solid grey;
  height: 20px;
  border-radius: 20px;
  margin-bottom: 20px;
  margin-top: 20px;
  position: relative;
  margin-left: auto;
  margin-right: auto;
}


.progressBar {
  width: 60%;
  background-color: rgba(255, 255, 255, 0.3);
  height: 20px;
  border-radius: 25px;
}

.progressIndicator {
  height: 100%;
  background-color: rgb(153, 30, 153);
  position: absolute;
  border-radius: inherit;
  width: var(--progressPercentage);
}

.questionnaireContainer {
  text-align: left;
  width: 700px;
  margin: auto;
  background-color: rgba(255, 255, 255, 0.8);
  padding: 20px;
  border-radius: 25px;
  display: flex;
}

.submtBtn,
.canclBtn {
  padding: 10px;
  margin-top: 20px;
  border: none;
  border-radius: 15px;
  width: 10%;
}

.submtBtn {
  
  cursor: pointer;
  font-size: medium;
  background-color:rgba(236, 15, 207, 0.4);
}

.canclBtn {
  
  font-size: medium;
  background-color:rgba(0, 0, 0, 0.6);
}
.btn{
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
 
}

.messageBox {
  width: 40%;
  border-radius: 25px;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 1;
  text-align: center;
  justify-content: center;
  display: flex;  
  flex-direction: column;
}


.message {
  background-color:rgba(99, 121, 245, 0.178);
  border: 0.1rem solid #386bf7;
  border-radius:3rem;
  margin: 0 auto;
  width: 80%;
  height: 30vh;


  
  
}
.message p{
  color: rgb(153, 30, 153);
}
.message h4{
 color: rgba(2, 6, 236, 0.5);
}

@media screen and (max-width: 768px) {

  .questionnaireContainer{
    width: 80%;
    height: fit-content;
    font-size: small;
  }
  .questions p{
    font-size:1rem ;
  }
  .messageBox{
    width: 80%;
    display: flex;
    flex-direction: column;
  }
  .message {
    width:100%;
    font-size: 10px;
    height: 40vh;
  }
  .backbutton{
    display: flex;
    flex-direction: column;
    margin: 0 auto;
  }
  .backbutton button{
    display: flex;
    flex-direction: column;
   
   width: fit-content;
   margin: 0.51rem auto;
  }
  .btn{
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
   
  }
  .btn button{
    width: fit-content;
    margin: 1rem;
  }
}